



















body {
  height: 100%;
}
.app {
  height: 100%;
}
.app .side-bar {
  height: 100%;
}
.app .ant-menu {
  height: 100%;
  background-color: #232323;
}
.app .ant-menu .ant-menu-item:hover {
  background: inherit;
}
.app .ant-menu .ant-menu-item:hover>a {
  border-left: 2px solid #fff;
  color: #fff;
}
.app .ant-menu .ant-menu-item.ant-menu-item-selected {
  border-right: 0px solid #fff;
}
.app .ant-menu .ant-menu-item.ant-menu-item-selected>a {
  border-left: 2px solid #fff;
  color: #fff;
}
.app .ant-menu .ant-menu-item>a {
  display: inline;
  padding-left: 10px;
  color: #a6a6a6;
}
.app .ant-menu-inline>.ant-menu-item {
  padding: 0 20px;
}
.app .header-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  width: 100%;
  background-color: #006c92;
}
.app .header-bar-logo {
  height: 18px;
  margin: auto 18px;
}
.app .ant-col-21 {
  height: 100%;
  overflow: auto;
}



